* {
  background-color: black;
}

/* SlideNavbar.css */
.slide-navbar {
  position: fixed;
  top: 0;
  right: -250px;
  height: 100%;
  width: 250px;
  background-color: #333;
  transition: right 0.3s;
}

.slide-navbar.active {
  right: 0;
}

.toggle-button {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1; /* Ensure the button is above the navbar */
}
